.header-btn-menu-wrap {
  width: 100%;
  height: 125px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.startup-speed-btn {
  border: 1px solid #aaa;
  height: 30px;
  line-height: 30px;
  padding: 14px 32px;
  font-size: 17.2px;
  text-align: center;
  cursor: pointer;
}
.icon-box {
  width: 80px;
  display: flex;
}

.icon-wrap {
  flex: 0 0 40px;
  line-height: 40px;
}
